@import "_partial/_config";

/* background */
.forfansEvent,
.forfansFanclub,
.forfansGoods {
	background: url(/img/img01.jpg) no-repeat center center;
	background-size: cover;
	background-attachment: fixed;
}

/* -----------------------------------------------------------------------
## event #################################################################
----------------------------------------------------------------------- */
.events {
	padding-top: 16px;
	.mainSubBox {
		margin-top: 32px;
		&_main {
			padding-right: 16px;
		}
		&_sub {
			margin-left: 16px;
			padding: 8px;
			border-top: 1px solid $color_line;
		}
	}
	.archiveList {
		padding-bottom: 16px;
		border-bottom: 1px solid $color_line;
		@include fontsize(14);
		&_item {
			margin-top: 8px;
		}
	}
	.mainSubBox_block {
		margin-top: 64px;
		&:first-child {
			margin-top: 0;
		}
	}
	.block_img {
		img {
			width: 100%;
		}
	}
	.block_title {
		margin-top: 16px;
		@include fontsize(20);
	}
	.block_date {
		margin-top: 8px;
		@include fontsize(14);
	}
	.block_text {
		line-height: 1.5;
		margin-top: 8px;
		@include fontsize(14);
	}
	.block_btn {
		margin-top: 8px;
		a {
			border: 1px solid $color_line;
		}
	}
	.pager {
		display: flex;
		margin-top: 64px;
		.pagenation {
			flex: 1;
			text-align: center;
			&_item {
				display: inline-block;
				a {
					display: block;
					line-height: inherit;
					padding: 8px 14px;
					border: 2px solid $color_line;
					border-radius: 0;
				}
				&.active span {
					display: block;
					padding: 8px 14px;
					border: 2px solid $color_line;
					background: $color_bg_light;
					color: $color_text_dark;
					font-weight: bold;
				}
			}
		}
	}
}
@include mediaQuery(sp){
	.events {
		.mainSubBox {
			margin-top: 16px;
			&_main {
				padding-right: 0;
			}
			&_sub {
				margin-top: 16px;
				margin-left: 0;
				padding: 0;
			}
		}
		.mainSubBox_block {
			margin-top: 32px;
		}
		.asideTitle {
			padding-top: 4px;
		}
		.pager {
			display: block;
			&_prev,
			&_next {
				text-align: center;
				a {
					display: block;
					border: 1px solid $color_line;
				}
			}
			.pagenation {
				display: block;
				padding: 8px 0;
			}
		}
	}
}


/* -----------------------------------------------------------------------
## fanclub ###############################################################
----------------------------------------------------------------------- */
.fanclubs {
	padding-top: 16px;
	&_mainvisual {
		position: relative;
		margin-top: 32px;
		padding-top: 56.308%;
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
	.about {
		&_body {
			line-height: 1.5;
			margin-top: 16px;
			@include fontsize(14);
		}
	}
	.privilege {
		.col4Box {
			line-height: 1.5;
			margin-top: 16px;
			@include fontsize(14);
		}
		&_title {
			margin-top: 16px;
			text-align: center;
		}
		&_body {
			margin-top: 16px;
		}
	}
	.join {
		.col2Box {
			line-height: 1.5;
			margin-top: 16px;
			@include fontsize(12);
		}
		&_title {
			text-align: center;
			@include fontsize(16);
		}
		&_step {
			margin-top: 8px;
			border-top: 2px solid $color_line;
			text-align: center;
		}
		&_step_item {
			position: relative;
			padding: 16px 8px;
			background: $color_bg;
			border: 2px solid $color_line;
			border-top: none;
			&:before {
				position: absolute;
				top: -2px;
				left: 0;
				right: 0;
				z-index: 2;
				margin: auto;
				content: "";
				letter-spacing: 0;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 11px 7.5px 0 7.5px;
				border-color: $color_bg transparent transparent transparent;
			}
			&:after {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				z-index: 1;
				margin: auto;
				content: "";
				letter-spacing: 0;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 13px 8.5px 0 8.5px;
				border-color: $color_line transparent transparent transparent;
			}
			&:first-child:before,
			&:first-child:after {
				display: none;
			}
		}
	}
}
@include mediaQuery(sp){
	.fanclubs {
		.privilege {
			.col4Box_item {
				margin-top: 32px;
			}
			&_img {
				img {
					width: 100%;
				}
			}
			&_title {
				margin-top: 8px;
			}
			&_body {
				margin-top: 8px;
			}
		}
		.join {
			.col2Box_item {
				margin-top: 32px;
				&:first-child {
					margin-top: 0;
				}
			}
		}
	}
}


/* -----------------------------------------------------------------------
## goods #################################################################
----------------------------------------------------------------------- */
.goods {
	padding-top: 16px;
	.overview {
		margin-top: 16px;
	}
	&_list {
		margin-top: 32px;
	}
	&_name {
		margin-top: 8px;
		@include fontsize(20);
	}
	&_price {
		margin-top: 8px;
		.icon {
			display: inline-block;
			margin-right: 8px;
			padding: 0 8px;
			background: red;
		}
	}
	&_intro {
		margin-top: 8px;
	}
	&_buy {
		margin-top: 8px;
		a {
			display: block;
			border: 1px solid $color_line;
		}
	}
	.col2Box {
		flex-wrap: wrap;
	}
	.col2Box_item {
		margin-top: 48px;
		width: 44.784%;
		flex: 0 1 auto;
	}
	&_others {
		.col2Box_item {
			padding: 16px;
			background: $color_bg_light;
			color: $color_text_dark;
		}
		&_title {
			@include fontsize(24);
			font-weight: normal;
		}
		.imgTxtBox {
			margin-top: 8px;
			@include imgTxtBox(68px, 308px);
		}
		.app_bnr {
			margin-top: 8px;
			li {
				display: inline-block;
				width: 135px;
			}
			img {
				width: 100%;
			}
		}
	}
}
@include mediaQuery(sp){
	.goods {
		&_img {
			img {
				width: 100%;
			}
		}
		&_intro {
			line-height: 1.5;
			margin-top: 8px;
			@include fontsize(14);
		}
		.col2Box_item {
			width: 100%;
		}
		&_others {
			&_title {
				@include fontsize(20);
			}
			.imgTxtBox {
				float: none;
				.txtBox {
					float: none;
					width: 100%;
					line-height: 1.5;
					@include fontsize(14);
				}
			}
		}
	}
}